.bottom{
    height: 1605px;
    &__bg{
        position: absolute;
        width: 100%;
        height: fit-content;
        &__overlay{
            width: 100%;
            height: 100%;
            position: absolute;
            background: var(--iris-color);
            mix-blend-mode: overlay;
            margin-top: 40px;
        }
        &__gradient{
            height: 824px;
            width: 100%;
            background: linear-gradient(180deg, #18183C 0%, rgba(24, 24, 60, 0) 100%);
            position: absolute;
            margin-top: 40px;
        }
    }
    &__circleWrap{
        position: absolute;
        width: 100%;
        height: 100%;       
        display: flex;
        justify-content: center;
        &__circle{
            width: 1708px;
            height: 1708px;
            opacity: 0.1;
            border: 2px solid var(--white-color);
            margin-top: 298px;
            border-radius: 100%;
        }
    }
    &__content{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: end;
        height: 100%;
        &__headline{
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 56px;
            z-index: 1;
            img{
                width: 296px;
                height: 30px;
            }
            p{
                @include poppins56();
                letter-spacing: 0.02em;
                color: var(--white-color);
                max-width: 634px;
                text-align: center;
            }
        }
        &__contacts{
            padding-top: 200px;
            display: flex;
            justify-content: space-between;
            gap: 181px;
            h4{
                @include poppins16();
                letter-spacing: 0.02em;
                color: var(--white-color);
                padding-bottom: 16px;
                z-index: 1;
            }
            p{
                @include poppins16();
                letter-spacing: 0.02em;
                color: var(--white-color);
                opacity: 0.7;
            }
            a{
                &:link, &:visited,&:active{
                    text-decoration: none;
                    color: var(--white-color);
                }
            }
            &__item{
                display: flex;
                flex-direction: column;
            }
            &__item{
                &-underline{
                    text-decoration: underline;
                }
            }
        }
        &__copyright{
            padding-top: 184px;
            padding-bottom: 40px;
            p{
                @include poppins16_300();
                letter-spacing: 0.04em;
                color: var(--white-color);
                opacity: 0.7;
            }
        }
    }
}