.text{
    height: 944px;
    &__content{
        padding-top: 260px;
        &__divider{
            display: flex;
            align-items: center;
            gap: 32px;
            p{
                @include poppins16();
                letter-spacing: 0.1em;
                color: var(--white-color);
                opacity: 0.4;
                text-transform: uppercase;
            }
            &__line{
                height: 1px;
                width: 300%;
                background-color: var(--white-color);
                opacity: 0.16;
                flex-shrink: 0;
            }
        }
        h2{
            @include poppins56();
            padding-top: 48px;
            letter-spacing: 0.02em;
            color: var(--white-color);
            max-width: 850px;
        }
        &__block{
            @include poppins16_300();
            letter-spacing: 0.04em;
            color: var(--white-color);
            opacity: 0.8;
            padding-top: 56px;
            padding-left: 72px;
            max-width: 912px;
        }
        &__numbers{
            display: flex;
            gap: 215px;
            padding-top: 148px;
            padding-left: 72px;
            &__item{
                display: flex;
                flex-direction: column;
                h3{
                    @include poppins56_500();
                    letter-spacing: 0.04em;
                    color: var(--white-color);
                }
                p{
                    @include poppins16_300();
                    letter-spacing: 0.04em;
                    opacity: 0.8;
                    color: var(--white-color);
                    padding-top: 24px;
                }   
            }
        }
    }
}